import React, {Component} from 'react';
import Goalnodes from './components/Goalnodes';
import SVGModel from './components/SVGModel';
import * as d3 from 'd3';
import problemData from './problem_data.js';

class App extends Component {
  constructor() {
    super();
    this.state = {prob : 1, answer1 : 1, answer3 : 1, ans1Entered : false};
    this.handleAnswer1 = this.handleAnswer1.bind(this);
    this.handleAnswer2 = this.handleAnswer2.bind(this);
    this.handleAnswer3 = this.handleAnswer3.bind(this);
    this.handleAnswer4 = this.handleAnswer4.bind(this);
    this.nextProblem = this.nextProblem.bind(this);
  }
  componentDidMount() {
     d3.select('#prob1').style('visibility', 'visible');
  }
  handleAnswer1(evt) {
     let response = Number(document.getElementById('denom').value);
     if (evt.keyCode === 13) {
     if (response === problemData[this.state.prob - 1].fracDenom) {
     d3.select('#denom').style('box-shadow', 'none').style('font-weight', 900).attr('disabled', 'disabled')
       .style('border', '1px solid green').style('background-color', 'transparent');
     }
     }
     else {
     if (response === 0 || response === '') {
        response = 1;
     }
     this.setState({answer1 : response, ans1Entered : true}, function() {
        if (document.getElementById('numer').value !== '') {
        for (var a = 1; a < this.state.answer3 + 1; a++) {
        d3.select(`#section${a}`).style('fill', 'rgba(0, 0, 255, 0.1)');
        }
        }
     });
     }
     if (problemData[this.state.prob - 1].svgType === 'number-line' && document.getElementById('numer').value !== '') {
     let ans3 = document.getElementById('numer').value;
     let ans1 = document.getElementById('denom').value;
     setTimeout(function(){
     let svgWidth = 0.41 * window.innerWidth - 25;
     d3.select('#shaderRect').attr('width', (ans3 / ans1) * ((svgWidth - 30) / (Math.ceil(ans3 / ans1))));
     }, 100);
     }
  }
  handleAnswer2() {
     let response = document.getElementById('wholeName').value;
     if (response === problemData[this.state.prob - 1].txtans) {
     d3.select('#wholeName').style('font-weight', 900).attr('disabled', 'disabled')
       .style('border', '1px solid green').style('background-color', 'transparent');
     }
  }
  handleAnswer3(evt) {
     let response = Number(document.getElementById('numer').value);
     if (problemData[this.state.prob - 1].svgType === 'set') {
     d3.selectAll('.vertSection').style('fill', 'rgba(255, 255, 255, 1)');
     for (var a = 1; a < response + 1; a++) {
        d3.select(`#section${a}`).style('fill', 'rgb(0, 0, 255, 0.1)');
     }
     if (response === problemData[this.state.prob - 1].fracNum) {
     d3.select('#numer').style('box-shadow', 'none').style('font-weight', 900).attr('disabled', 'disabled')
       .style('border', '1px solid green').style('background-color', 'transparent');
     }
     }
     else {
     if (response === '') {
        document.getElementById('numer').value = '0';
        response = 0;
     }
     if (problemData[this.state.prob - 1].svgType === 'area') {
     d3.selectAll('.vertSection').style('fill', 'rgba(255, 255, 255, 1)');
     setTimeout(function(){
     for (var a = 1; a < response + 1; a++) {
        d3.select(`#section${a}`).style('fill', 'rgba(0, 0, 255, 0.1)');
     }
     }, 50);
     }
     else if (problemData[this.state.prob - 1].svgType === 'number-line') {
     let ans3 = document.getElementById('numer').value;
     let ans1 = this.state.answer1;
     setTimeout(function(){
     let svgWidth = 0.41 * window.innerWidth - 25;
     d3.select('#shaderRect').attr('width', (ans3 / ans1) * ((svgWidth - 30) / (Math.ceil(ans3 / ans1))));
     }, 100);
     }
     this.setState({answer3 : response});
     if (evt.keyCode !== 13) return;
     if (response === problemData[this.state.prob - 1].fracNum) {
     d3.select('#numer').style('box-shadow', 'none').style('font-weight', 900).attr('disabled', 'disabled')
       .style('border', '1px solid green').style('background-color', 'transparent');
     }
     }
  }
  handleAnswer4() {
     let response = document.getElementById('estimate').value;
     if (response === problemData[this.state.prob - 1].estimate) {
     d3.select('#estimate').style('font-weight', 900).attr('disabled', 'disabled')
       .style('border', '1px solid green').style('background-color', 'transparent');
     }
  }
  nextProblem() {
     let newprob;
     if (this.state.prob === problemData.length) newprob = 1;
     else newprob = this.state.prob + 1;
     this.setState({prob : newprob, answer1 : 1, answer3 : 1, ans1Entered : false});
     setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub], function () {
        d3.select(`#prob${newprob}`).style('visibility', 'visible');
        });}, 100);
  }
  render() {
    return (
      <div>
      <button className='nextButton' onClick={this.nextProblem}>next</button>
      <div className='container' id='goalnodes_container'>
      <Goalnodes probnum={this.state.prob} ans1={this.handleAnswer1} ans2={this.handleAnswer2} ans3={this.handleAnswer3} ans4={this.handleAnswer4} ans1Val={this.state.answer1} ans1Submit={this.state.ans1Entered} />
      </div>
      <div className='container' id='model_container'>
      <SVGModel probnum={this.state.prob} ans1Val={this.state.answer1} ans3Val={this.state.answer3} />
      </div>
      </div>
    );
  }
}

export default App;
