import React, {Component} from 'react';
import problemData from '../problem_data';

class SVGModel extends Component {
   constructor() {
      super();
      this.state = {};
   }
   render() {
     let vertSections = [];
     let svgs = [];
     let circs = [];
     let numSVGs = Math.ceil(this.props.ans3Val / this.props.ans1Val);
     if (numSVGs === 0) numSVGs = 1;

     if (problemData[this.props.probnum - 1].svgType === 'area') {
        let secs = this.props.ans1Val || 1;
        let svgHeight = (0.62 * window.innerHeight - 25) / numSVGs;

        for (var b = 0; b < secs * numSVGs; b++) {
        let id = `section${b + 1}`;
        vertSections.push(
        <rect key={b} id={id} className='vertSection' x='0' y={svgHeight - ((b % secs + 1) * (svgHeight / secs))} width='100%' height={svgHeight / secs} style={{fill : 'none', stroke : 'gray', strokeDasharray : '5 5'}} />
        );
        }

        for (var c = 0; c < numSVGs; c++) {
        svgs.push(
        <svg key={c} className='vertSVG' width='100%' height={svgHeight} preserveAspectRatio='xMidYMid'>
        {vertSections.slice(c * (secs), secs * (c + 1))}
        </svg>
        );
        }
        return (<div>{svgs.reverse()}</div>);
     }
     else if (problemData[this.props.probnum - 1].svgType === 'set') {
        let svgHeight = 0.62 * window.innerHeight - 25;
        let svgWidth = 0.41 * window.innerWidth - 25;
        let secs = this.props.ans1Val || 1;
        let rad = Math.min(svgHeight, svgWidth) / (secs * 2.05);

        for (var f = 0; f < secs; f++) {
        let randX = Math.floor(Math.random() * ((svgWidth - (rad + 10)) - (rad + 10) + 1)) + (rad + 10);
        let randY = Math.floor(Math.random() * ((svgHeight - (rad + 10)) - (rad + 10) + 1)) + (rad + 10);
        circs.push(
        <circle key={f} id={`section${f + 1}`} className='vertSection' cx={randX} cy={randY} r={rad} style={{stroke : 'gray', fill : 'white'}} />
        );
        }
        return (
        <svg width='100%' height={svgHeight} preserveAspectRatio='xMidYMid'>
        {circs}
        </svg>
        );
     }
     else if (problemData[this.props.probnum - 1].svgType === 'number-line') {
        let ticks = [];
        let numLineText = [];
        let svgHeight = 0.62 * window.innerHeight - 25;
        let svgWidth = 0.41 * window.innerWidth - 25;
        let secs = this.props.ans1Val * Math.ceil(this.props.ans3Val / this.props.ans1Val) || 0;

        for (var k = 0; k < secs; k++) {
        ticks.push(<path key={k} d={`M ${(k + 1) * ((svgWidth - 30) / secs) + 15} ${svgHeight / 2 - 7} L ${(k + 1) * ((svgWidth - 30) / secs) + 15} ${svgHeight / 2 + 7} Z`} style={{stroke : 'blue', strokeWidth : 1, fill : 'none'}} />);
        }
        for (var j = 0; j < Math.ceil(this.props.ans3Val / this.props.ans1Val) + 1; j++) {
        numLineText.push(<text key={j} x={j * ((svgWidth - 30) / (Math.ceil(this.props.ans3Val / this.props.ans1Val))) + 15} y={svgHeight / 2 + 25} style={{textAnchor : 'middle'}}>{j}</text>);
        }
        return (
        <svg width='100%' height={svgHeight} preserveAspectRatio='xMidYMid'>
        <path d={`M 15 ${svgHeight / 2 - 10} l 0 20 l 0 -10 L ${svgWidth - 15} ${svgHeight / 2} l 0 -10 l 0 20 l 0 -10`} style={{stroke : 'black', strokeWidth : 2, fill : 'none'}} />
        {ticks}
        {numLineText}
        <rect id='shaderRect' x='15' y={svgHeight / 2 - 7} width='0' height='14' style={{fill : 'rgba(0, 0, 255, 0.1)', stroke : 'none'}} />
        </svg>
        );
     }
   }
}

export default SVGModel;
