import React, {Component} from 'react';
import problemData from '../problem_data.js';

class Goalnodes extends Component {
  constructor() {
    super();
    this.state = {};
  }
  render() {
    let opener;
    if (problemData[this.props.probnum - 1].svgType !== 'number-line') {
       opener = `${problemData[this.props.probnum - 1].fraction} ${problemData[this.props.probnum - 1].text}`;
    }
    else opener = `${problemData[this.props.probnum - 1].text} ${problemData[this.props.probnum - 1].fraction}`;

    let verb, part;
    if (problemData[this.props.probnum - 1].fracDenom === 1) {verb = 'is'; part = 'part'}
    else {verb = 'are'; part = 'equal parts'}

    let select, select2, numInput;
    let options = [];
    let numInputOptions = [];
    for (var b = 0; b < problemData[this.props.probnum - 1].answers[0].q2.length; b++) {
       options.push(<option key={b} value={problemData[this.props.probnum - 1].answers[0].q2[b]}>{problemData[this.props.probnum - 1].answers[0].q2[b]}</option>)
    }
    select = (
      <select className='gnSelect' id='wholeName' onChange={this.props.ans2}>
      <option value=''></option>
      {options}
      </select>
    );
    if (problemData[this.props.probnum - 1].svgType !== 'set') {
       numInput = <input onKeyUp={this.props.ans3} disabled={this.props.ans1Submit ? '' : 'disabled'} type="text" id='numer' className='gnInput' maxLength='1' autoComplete='off' />;
    }
    else {
    for (var g = 0; g < problemData[this.props.probnum - 1].fracNum + 1; g++) {
       numInputOptions.push(<option key={g} value={g}>{g}</option>);
    }
    numInput = (
      <select className='gnSelect' id='numer' disabled={this.props.ans1Submit ? '' : 'disabled'} onChange={this.props.ans3}>
      <option value=''></option>
      {numInputOptions}
      </select>
    );
    }
    select2 = (
      <select className='gnSelect' id='estimate' onChange={this.props.ans4}>
      <option value=''></option>
      <option key='a' value='less than 1'>less than</option>
      <option key='b' value='equal to 1'>equal to</option>
      <option key='c' value='greater than 1'>greater than</option>
      </select>
    );

    let space;
    if (problemData[this.props.probnum - 1].fracDenom !== 1 && problemData[this.props.probnum - 1].svgType === 'set') {
       space = <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#8202;</span>;
    }
    else if (problemData[this.props.probnum - 1].fracDenom !== 1 && (problemData[this.props.probnum - 1].svgType === 'area'|| problemData[this.props.probnum - 1].svgType === 'number-line')) {
       space = <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#8202;</span>;
    }
    else if (problemData[this.props.probnum - 1].fracDenom === 1 && problemData[this.props.probnum - 1].svgType === 'area') {
       space = <span>&nbsp;&nbsp;&nbsp;&#8202;</span>;
    }
    if (problemData[this.props.probnum - 1].svgType === 'set') {
       space = <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>;
    }
    return (
    <div id={`prob${this.props.probnum}`} key={this.props.probnum} style={{visibility : 'hidden'}}>
    <p style={{color : 'teal'}}>Model the fraction <strong>{opener}</strong>.</p>
    <p className='gnode'>
    There {verb} <input onKeyUp={this.props.ans1} type="text" id='denom' className='gnInput' maxLength='2' autoComplete='off' /> {part} in {select}.
    </p>
    <p className='gnode'>
    {space}Count {numInput} part(s) to model the fraction {problemData[this.props.probnum - 1].fraction}.
    </p>
    <p className='gnode'>
    The fraction {opener} is {select2} 1 whole.
    </p>
    </div>
    );
   }
}

export default Goalnodes;
